.addTopicos {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  background: linear-gradient(
    to right top,
    rgba(255, 255, 255, 70%),
    rgba(255, 255, 255, 10%)
  );
  padding: 3rem;
  border-radius: 2rem;
  box-shadow: 1px 1px 4px;
  margin: 1rem;
}

.addForm {
  display: flex;
  flex-direction: column;
  align-items: space-between;
}

.addItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.submitBtn {
  margin-top: 1rem;
  width: 5rem;
  background-color: #49aec0;
  border: none;
  padding: 0.8rem;
  border-radius: 5px;
  cursor: pointer;
  opacity: 0.8;
}

.submitBtn:hover {
  opacity: 1;
  box-shadow: 1px 1px 4px;
}

.addInput {
  width: 30rem;
  height: 1rem;
  margin-bottom: 0.3rem;
}
