.navbar {
  width: 6rem;
  height: 100vh;
  position: fixed;
  background-color: #00887a;
  color: #ffffff;
  font-size: 1rem;
  transition: width 500ms ease;
  z-index: 20;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.nav-item {
  width: 100%;
  height: 80px;
}

.nav-item:last-child {
  margin-top: auto;
}

#book {
  width: 2.5rem;
  margin: 0 1rem;
}

i {
  font-size: 2.5rem;
  color: #999999;
  margin: 0 1rem;
}

.nav-link {
  display: flex;
  align-items: center;
  height: 5rem;
  text-decoration: none;
  transition: width 500ms ease;
}

.link-text {
  display: none;
  margin-left: 1rem;
}

.link-textOpen {
  display: inline-block;
  margin-left: 1rem;
}

.navbar:hover {
  width: 14rem;
  opacity: 0.95;
  z-index: 20;
}

.materiaShow li:hover {
  color: #f59323;
  width: 80%;
  background-color: #2a3e42;
  font-weight: bold;
  opacity: 1;
}

.navbar:hover .link-text {
  color: #ffffff;
  display: block;
  text-decoration: none;
}

.navbar:hover .nav-link:hover {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
  cursor: pointer;
}

.nav-item:hover {
  background-color: #344d52;
}

.navbar .nav-item:hover i {
  color: #f59323;
}

.navbarOpen {
  height: 100vh;
  position: fixed;
  background-color: #00887a;
  color: #ffffff;
  font-size: 1rem;
  width: 14rem;
  opacity: 0.9;
  z-index: 20;
}

.navbarOpen .link-text {
  color: #ffffff;
  display: block;
  text-decoration: none;
}

.navbarOpen .materiaShow li {
  border: 2px solid #ffffff;
  padding: 0.5rem;
  font-family: "Quicksand";
  cursor: pointer;
  opacity: 0.8;
}

.search {
  border-radius: 5px;
  border: 2px solid #db8727;
  width: 90%;
}

a {
  text-decoration: none;
  color: #ffffff;
}

.materiaWrapper {
  display: flex;
}

.card {
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-size: 1.5rem;
}

